<script setup lang="ts">
    import { GpnButton, GpnList, GpnPeriod, GpnSidebar, type ListItem } from "gpn-ui";
    import { useGlobalStore } from "../stores/store";
    import { ref } from "vue";

    const store = useGlobalStore();

    const sideShow = ref(false);
    const sideShow2 = ref(false);
    const sideShow3 = ref(false);
    const sideShowLeft = ref(false);
    const sideElem = ref<InstanceType<typeof GpnSidebar> | null>(null);
    const list = ref<ListItem[]>([]);
    const selList = ref<ListItem[]>([]);
    const dates = ref<string[]>([]);

    for (let i = 0; i < 40; i++) {
        list.value.push({
            id: i,
            name: `Значение в списке №${i}`,
        });
    }

    function openSide() {
        sideElem.value?.show();
    }
</script>

<template>
    <h2>Sidebar</h2>
    <div class="grid">
        <GpnButton :theme="store.theme" label="Open" @click="sideShow = true" />
        <GpnButton :theme="store.theme" label="Open left" @click="sideShowLeft = true" />
        <GpnButton :theme="store.theme" label="Open from code" @click="openSide" />
        <GpnPeriod v-model="dates" />

        <GpnSidebar v-model="sideShow" :theme="store.theme" title="Заголовок модального окна №1">
            <div>
                <GpnPeriod v-model="dates" />
            </div>
            <template #buttons="{ ok, cancel }">
                <div style="display: flex; gap: 8px">
                    <GpnButton label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                    <GpnButton label="Открыть еще..." :theme="store.theme" @click="sideShow2 = true" />
                </div>
                <GpnButton label="Отмена" view="secondary" :theme="store.theme" @click="cancel('cancel val')" />
            </template>
        </GpnSidebar>
        <GpnSidebar v-model="sideShow2" :width="300" :theme="store.theme" title="Заголовок внутреннего сайдокна №1">
            <template #buttons="{ ok, cancel }">
                <div style="display: flex; gap: 8px">
                    <GpnButton label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                    <GpnButton label="Открыть еще..." :theme="store.theme" @click="sideShow3 = true" />
                </div>
                <GpnButton label="Отмена" view="ghost" :theme="store.theme" @click="cancel('cancel val')" />
            </template>
        </GpnSidebar>
        <GpnSidebar v-model="sideShow3" :width="300" :theme="store.theme" title="Заголовок внутреннего сайдокна №2">
            <template #buttons="{ ok, cancel }">
                <GpnButton size="xl" label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                <GpnButton size="xl" label="Отмена" :theme="store.theme" view="ghost" @click="cancel('cancel val')" />
            </template>
        </GpnSidebar>

        <GpnSidebar v-model="sideShowLeft" side="left" :theme="store.theme" title="Заголовок модального окна №1">
            <template #buttons="{ ok, cancel }">
                <GpnButton label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                <GpnButton label="Отмена" :theme="store.theme" view="secondary" @click="cancel('cancel val')" />
            </template>
        </GpnSidebar>
        <GpnSidebar ref="sideElem" :theme="store.theme" title="Заголовок модального окна №2">
            <template #head>
                <div style="padding-bottom: 50px; border-bottom: 1px solid #ccc; width: 100%">Some fixed head</div>
            </template>
            <template #footer> Some fixed footer </template>
            <div style="width: 100%">
                <GpnList v-model="selList" multiple :items="list" is-clickable :theme="store.theme" size="m" />
            </div>
            <template #buttons="{ ok, cancel }">
